@import "../../../assets/styles/variables";

.section {
  width: 100%;
  background-color: $main-cl;
  display: flex;
  justify-content: center;
  position: absolute;
  align-items: center;
  bottom: 0px;
  left: 0px;
  width: 100%;
  min-height: 50px;
  & a {
    color: #fff;
    margin: 0px 5px;
    &:hover {
      color: #fff;
    }
  }
}